const reactAnimation = keyframes`
	from { transform: rotate(0deg); }
  	to { transform: rotate(360deg); }
`
const Svg = styled.svg`
	${({anim}) => anim && css`
		animation: ${reactAnimation} infinite 20s linear;
		`
	}
`

export const Icon = ({name, color = white, width = 36, height = 36}) => {
	
	return (
	<Svg fill={color} 
		version="1.1" 
		xmlns="http://www.w3.org/2000/svg" 
		width={width} 
		height={height}  
		viewBox={IconsNonStdViewBox[name] ? IconsNonStdViewBox[name] : "0 0 32 32"}
		anim={name === "reactAnim" ? name : null}
		>
	<title>{name}</title>
	{Array.isArray(Icons[name])
			? Icons[name].map((el, ind) => {
				return <path d={Icons[name][ind]}></path>	
			})
			: <path d={Icons[name]}></path>
	}
	{Array.isArray(Polygon[name]) 
		? Polygon[name] 
				? Polygon[name][0].map((el, ind, arr) => {
					let pts = el
					let filling
					Polygon[name][1].map((el2, ind2, arr2) => {
					  filling = arr2[ind]
					})			
					return <polygon 
						fill={filling}
						points={pts}
					></polygon>	
				
					})
				: null//}
		: <polygon 
				points={Polygon[name]}
			></polygon>	
	}
	{Rect[name] 
			? <rect
				width={Rect[name][0]}
				height={Rect[name][1]}
				x={Rect[name][2]}
				y={Rect[name][3]}
				rx={Rect[name][4]}
				fill={Rect[name][5]}
				>	
				</rect>
			: null
	}
	{Circle[name] 
			?  <circle
				cx={Circle[name][0]}
				cy={Circle[name][1]}
				r={Circle[name][2]}
				>	
				</circle> 
			: null	
	}			
	</Svg>

	//: null
	)
}



















